.photo {
  @include tablet {
    max-width: 702px;
    margin: 0 auto;
    // padding-bottom: 92px;
  }

  @include desktop {
    max-width: 1170px;
    margin: 0 auto;
    // padding-bottom: 92px;
  }
  padding: 0 15px 52px 15px;
}

.title-foto {
  @include desktop {
    font-size: 45px;
  }

  @include tablet {
    @include short-font(36px, 0.05em);
    margin: 0;
    padding: 0 0 32px 0;
  }

  @include font(32px, 700, 0.05em);

  font-family: 'Roboto Slab', serif;
  text-shadow: 3px 4px 10px $title-shadow;
  color: $primary-text;
  margin-left: 20px;
  padding: 0 0 16px 38px;
}

.subtitle-foto {
  @include tabletMin {
    font-size: 32px;
    // padding: 0 0 32px 20px;
  }

  @include font(30px, 500, 0.05em);

  font-family: 'EB Garamond', sans-serif;
  color: $secondary-color;
  padding: 0 0 32px 84px;
}

/*SWIPER*/
.swiper {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.swiper-slide {
  // background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  @include desktop {
    max-width: 1024px;
  }
  display: block;
  border-radius: 10px;
  // width: 100%;
  // height: 100%;
  object-fit: cover;
}
